import * as React from 'react';
import Container from '@mui/material/Container';

const Section = ({ children, id, maxWidth = 'lg', theme = 'light' }) => {
  const isLight = theme === 'light';

  return (
    <Container
      id={id}
      maxWidth={false}
      sx={{
        py: 9,
        backgroundColor: `primary.${isLight ? 'dark' : 'light'}`,
        color: `primary.${isLight ? 'light' : 'dark'}`
      }}
    >
      <Container maxWidth={maxWidth}>{children}</Container>
    </Container>
  );
};

export default Section;
