import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import MenuIcon from '@mui/icons-material/Menu';
import Logo from '../../images/logo.svg';
import { scrollToTop, scrollToSection } from '../../utils/scroll';

import Button from '@mui/material/Button';

const menuLists = ['ABOUT', 'SERVICES', 'COMPANY', 'CONTACT'];

const SectionMenu = () => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = _isOpen => event => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar position='fixed' color='inherit'>
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height: '68px'
          }}
        >
          <Box>
            <Link
              href='#top'
              sx={{
                display: 'block'
              }}
              underline='none'
              onClick={e => {
                e.preventDefault();
                scrollToTop();
              }}
            >
              <img
                src={Logo}
                width={413}
                height={64}
                style={{
                  width: 206.5,
                  height: 32
                }}
              />
            </Link>
          </Box>
          <Box>
            {menuLists.map((title, i) => {
              const lowerTitle = title.toLowerCase();
              const isContact = title === 'CONTACT';

              return (
                <Button
                  key={i}
                  href={`#${lowerTitle}`}
                  variant={isContact ? 'outlined' : 'text'}
                  sx={{
                    margin: '0 1em',
                    borderRadius: 0,
                    letterSpacing: '0.1em',
                    display: {
                      xs: 'none',
                      md: 'inline'
                    }
                  }}
                  onClick={e => {
                    e.preventDefault();
                    scrollToSection(lowerTitle);
                  }}
                >
                  {title}
                </Button>
              );
            })}
            <IconButton
              onClick={toggleDrawer(true)}
              sx={{
                display: {
                  xs: 'block',
                  md: 'none'
                }
              }}
            >
              <MenuIcon />
            </IconButton>
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          sx={{
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {menuLists.map((sectionName, i) => (
              <ListItem
                button
                key={i}
                onClick={e => {
                  e.preventDefault();
                  scrollToSection(sectionName.toLowerCase());
                }}
              >
                <ListItemText
                  primary={<Typography>{sectionName}</Typography>}
                />
              </ListItem>
            ))}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};

export default SectionMenu;
